@import '../../styles/sass/variables';
@import '../../styles/sass/mixins';

.title {
    margin:0 0 30px;
    font-family: 'Josefin Sans', sans-serif;
    color: $textColor;
    text-transform: capitalize;
    line-height: 1.2;
    letter-spacing: 5px;
    position: relative;
    text-align: center;
    & h2 {
        font-size: 36px;
        margin-top: 30px;
        font-weight: 400;
    }
    &::before {
        content: "";
        background: $primaryColor;
        width: 55px;
        height: 1px;
        position: absolute;
        left: 50%;
        transform: translate(-50%, 50%);
        bottom: 0;
    }
}





